"use client";
import Image from "next/image";
import { useRouter } from "next/navigation";
import React, { useEffect , useState } from 'react'
import logo from "../public/static/images/logo.png";
import api from "@/lib/axios";
import { isAuthenticated } from "@/utility/auth";


export default function Login() {

    const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [message, setMessage] = useState('');
  const [error, setError] = useState(null);
  const router = useRouter();

  const handleLogin = async (e) => {
    e.preventDefault();
    setError(null);
    try {
      const response = await api.post(`${process.env.NEXT_PUBLIC_BACKEND_URL_API}/login`, {
        email,
        password,
      },
      { headers: { "Content-Type": "application/json" } }
      
    );
    
    // localStorage.setItem('auth_token', response.data.token);
    useEffect(() => {
    if (isAuthenticated()) {
      router.push('/dashboard');
    }
  }, [router]);
    } catch (err) {
      setError(err.response?.data?.message || "Invalid credentials");
    }
  };
  return (
    <>
      <div className="flex h-screen items-center justify-center p-10">
            <div className="xl:w-1/2  rounded-2xl border border-white-800 md:shadow-xl">
          <div className="grid grid-cols-3 gap-4 p-5">
            <div className="flex items-center justify-items-center">
              <Image src={logo} alt="" />
            </div>
            <div className=" items-center justify-center col-span-2">
              <form onSubmit={handleLogin}>
                <h1 className="text-center font-extrabold uppercase text-[#FF6B00]">Login Panel</h1>
                <br />
                <input  name="email" id="email" type="text" className="mb-3 w-full rounded-2xl outline-rose-400 px-5 py-3 text-gray-950 text-[#692c6c]" placeholder="email"  onChange={(e) => setEmail(e.target.value)}
            required/>
                <div className="text-red-500">
                  {/* {errors.email?.message} */}
               </div>
 
                <input id="password" type="password" className="mb-3 w-full rounded-2xl outline-rose-400 px-5 py-3 text-[#692c6c]" placeholder="password" onChange={(e) => setPassword(e.target.value)}
            required/>
{error && <p style={{ color: "red" }}>{error}</p>}
               <button type="submit" className="mb-3 w-full rounded-2xl bg-rose-500 px-5 py-3 font-semibold bg-[#FF6B00] text-white">Login</button>
              </form>
            </div>
          </div>
        </div>
      </div>
         
    </>
  );
// }
}